<template>
    <div>
        
    </div>
</template>

<script>
import PostService from '../PostService';
export default {
    name: 'HelloWorld',
    data() {
        return {
            posts: [],
            error: '',
            text: ''
        }
    },
    async created() {
        try {
            this.posts = await PostService.getPosts();
        } catch (err) {
            this.error = err.message;
        }
    }
}
</script>

